import React, { useEffect } from "react"
import { Header } from "../common/Header.jsx"
import { Container } from "@mui/material"
import { CollectionTable } from "../Table/TableContainer.jsx"
import { InputBox } from "../common/InputBox.jsx"
import { checkIfCollectionExists, createCollection } from "../../utils/services/macrometaCollectionsOperation.js"
import { stringUtils } from "../../utils/constants/stringUtils.js"

export const Home = () => {
    const createCollectionIfNotExists = async () => {
        const response = await checkIfCollectionExists(stringUtils.MACROMETA_COLLECTION_NAME)
        if (!response) {
            await createCollection(stringUtils.MACROMETA_COLLECTION_NAME)
        }
    }
    useEffect(() => {
        createCollectionIfNotExists()
    }, [])

    return (
        <Container maxWidth={false}>
            <Header />
            <InputBox />
            <CollectionTable />
        </Container>
    )
}
